import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { StaticImage } from "gatsby-plugin-image";
import toast, { Toaster } from "react-hot-toast";
import ClipboardButton from "react-clipboard.js";
import socials from "../data/socials";
import useLinkCursor from "../hooks/useLinkCursor";

export default function Contact() {
  const [sent, setSent] = useState(false);
  useLinkCursor();
  async function sendMail(e) {
    e.preventDefault();
    const XHR = new XMLHttpRequest();
    const formData = new FormData(e.target);
    XHR.open("POST", "/mail.php");
    XHR.send(formData);
    setSent(true);
  }
  const copied = (copy) => () => {
    toast.success(copy + " copied to clipboard!", {
      style: {
        border: "1px solid #252525",
        padding: "8px 16px",
        color: "#252525",
        marginTop: 50,
      },
      iconTheme: {
        primary: "#252525",
        secondary: "#DDD",
      },
    });
  };
  return (
    <>
      <Toaster position="top-center" reverseOrder={false} />
      <div className="contact-container">
        <div className="left">
          <div className="contact-details">
            <StaticImage
              width={280}
              height={373}
              src={"../images/shakil.jpg"}
              alt="Shakil Ahmmed"
            />
            <strong>Shakil Ahmmed</strong>
            <ClipboardButton
              className="email"
              component="a"
              button-href="#"
              data-clipboard-text="hi@shakil.info"
              onSuccess={copied("Email")}
            >
              hi@shakil.info
            </ClipboardButton>
            <ClipboardButton
              className="tel"
              component="a"
              button-href="#"
              data-clipboard-text="+8801861590250"
              onSuccess={copied("Phone number")}
            >
              +8801861590250
            </ClipboardButton>
            <ClipboardButton
              component="a"
              button-href="#"
              data-clipboard-text="PO 1213, Dhaka, Bangladesh"
              onSuccess={copied("Address")}
            >
              PO 1213, Dhaka, Bangladesh
            </ClipboardButton>
            <div className="df jcc aie socials cp">
              {socials.map(({ a, icon }) => (
                <a
                  key={a.title}
                  href={a.href}
                  title={a.title}
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <span>
                    <FontAwesomeIcon icon={icon} />
                  </span>
                </a>
              ))}
            </div>
          </div>
        </div>
        <div className="right">
          <form className="contact-form" onSubmit={sendMail}>
            <label className="f-label">
              <input
                name="name"
                placeholder=" "
                type="text"
                disabled={sent}
                required="required"
              />
              <span className="label">Name *</span>
            </label>
            <label className="f-label">
              <input
                name="email"
                placeholder=" "
                type="email"
                disabled={sent}
                required="required"
              />
              <span className="label">Email *</span>
            </label>
            <label className="f-label">
              <textarea
                name="message"
                data-gramm_editor="false"
                placeholder=" "
                disabled={sent}
                required="required"
                rows="5"
              ></textarea>
              <span className="label">Message *</span>
            </label>
            {sent && (
              <p
                style={{
                  color: "mediumseagreen",
                  fontWeight: "bold",
                  margin: 10,
                  textAlign: "center",
                }}
              >
                Message sent successfully! Thank you.
              </p>
            )}
            <button type="submit" disabled={sent}>
              Say Hi!
            </button>
          </form>
        </div>
      </div>
    </>
  );
}
